async function loadEXRAssets()
{
    let resolve, reject;
    let resolve2, reject2;
    const p = new Promise((res, rej) =>
    {
        resolve = res;
        reject = rej;
    });

    exrLoader.load("./maps/env-gem-4.exr", function (texture)
    {
        texture.mapping = THREE.EquirectangularReflectionMapping;
        // exrCubeRT = pmremGenerator.fromEquirectangular(texture);
        // cubeRT = new THREE.WebGLCubeRenderTarget(texture.image.width)
        // environments.mainEXRCube = cubeRT.fromEquirectangularTexture(renderer, exrCubeRT.texture)
        environments.mainEXR = texture;
        window.environments = environments;
        resolve(true);
    });

    const p2 = new Promise((res, rej) =>
    {
        resolve2 = res;
        reject2 = rej;
    });

    exrLoader.load("./maps/env-metal-14.exr", function (texture)
    {
        texture.mapping = THREE.EquirectangularReflectionMapping;
        const exrCubeRT = pmremGenerator.fromEquirectangular(texture);
        const cubeRT = new THREE.WebGLCubeRenderTarget(texture.image.width)
        environments.metalEXRCube = cubeRT.fromEquirectangularTexture(renderer, exrCubeRT.texture)
        environments.metalEXR = texture;
        window.environments = environments;
        resolve2(true);
    });

    return Promise.all([p, p2]);
}

function loadModelAssets()
{
    const loadingPromises = [];

    const loadModel = (i) =>
    {
        let resolver;

        const p = new Promise((res, rej) =>
        {
            resolver = res;
        });

        // Setting promises and glbs using index to ensure correctness of models order.
        // In this case order is going to match modelAssets list.
        loadingPromises[i] = p;

        gltfLoader.load(modelAssets[i], (glbModel) =>
        {
            loadedGlbs[i] = glbModel;
            resolver();
        });
    }

    for (let i = 0; i < modelAssets.length; i++)
    {
        loadModel(i);
    }

    return loadingPromises;
}

