function applyPhysicalMaterialProps(material, props)
{
    for (let i in props)
    {
        if (props[i] instanceof THREE.Color)
        {
            material[i].set(props[i]);
        }
        else
        {
            material[i] = props[i];
        }
    }
}

function setupTransmissionMaterial()
{
    const transmissionMaterial = Object.assign(new MeshTransmissionMaterial(7), {
        clearcoat: 1,
        clearcoatRoughness: 0,
        transmission: 1,
        chromaticAberration: 0.03,
        anisotrophicBlur: 0.1,
        side: THREE.DoubleSide,
        // Set to > 0 for diffuse roughness
        roughness: 0,
        thickness: 7.5,
        ior: 2.5,
        // Set to > 0 for animation
        distortion: 0.1,
        distortionScale: 0.2,
        temporalDistortion: 0.2
    });

    const transmissionMaterialSmall = Object.assign(new MeshTransmissionMaterial(7), {
        clearcoat: 1,
        clearcoatRoughness: 0,
        transmission: 1,
        chromaticAberration: 0.03,
        anisotrophicBlur: 0.1,
        side: THREE.DoubleSide,
        // Set to > 0 for diffuse roughness
        roughness: 0,
        thickness: 2.5,
        ior: 2.5,
        // Set to > 0 for animation
        distortion: 0.1,
        distortionScale: 0.2,
        temporalDistortion: 0.2
    });

    physicalMaterials.transmissionMaterial = transmissionMaterial;
    physicalMaterials.transmissionMaterialSmall = transmissionMaterialSmall;
    utils.addPhysicalMaterialToGUI(transmissionMaterial, gui);
}

function setupGemMaterial(gemMaterial, envMap)
{
    const defines = {};
    // const isCubeMap = isCubeTexture(envMap)
    // const envMap = environments.main;
    // const envMap = environments.mainEXRCube.texture;
    // const envMap = environments.mainEXR;
    const isCubeMap = envMap.isCubeTexture;
    const w = (isCubeMap ? envMap.image[0]?.width : envMap.image.width) ?? 1024
    const cubeSize = w / 4
    const _lodMax = Math.floor(Math.log2(cubeSize))
    const _cubeSize = Math.pow(2, _lodMax)
    const width = 3 * Math.max(_cubeSize, 16 * 7)
    const height = 4 * _cubeSize
    if (isCubeMap)
    {
        defines.ENVMAP_TYPE_CUBEM = ''
        // defines.ENVMAP_TYPE_CUBE = ''
    }

    // defines.CUBEUV_TEXEL_WIDTH = `${1.0 / width}`
    // defines.CUBEUV_TEXEL_HEIGHT = `${1.0 / height}`
    // defines.CUBEUV_MAX_MIP = `${_lodMax}.0`
    // Add defines from chromatic aberration
    if (refractionMaterialProps.diamond.aberrationStrength > 0)
    {
        defines.CHROMATIC_ABERRATIONS = ''
    }

    if (refractionMaterialProps.diamond.fastChroma)
    {
        defines.FAST_CHROMA = ''
    }

    gemMaterial.defines = Object.assign(gemMaterial.defines, defines);
    // gemMaterial.envMap = envMap;
    gemMaterial.envMapD = envMap;
}

function createGemMaterial()
{
    const gemMaterial = Object.assign(new MeshGemMaterial(), refractionMaterialProps.diamond);
    const r = new THREE.Vector2();
    renderer.getSize(r);
    gemMaterial.resolution = r;
    gemMaterial.side = THREE.FrontSide;
    // gemMaterial.envMap = envMap;
    // gemMaterial.envMapD = envMap;
    gemMaterial.reflectivity = 1;
    gemMaterial.roughness = 0.007;
    gemMaterial.metalness = 0;
    gemMaterial.color = new THREE.Color("#ffffff");
    gemMaterial.transmission = 0//0.032;
    gemMaterial.ior = 2.44;
    gemMaterial.thickness = 1.3;

    setupGemMaterial(gemMaterial, environments.mainEXR);

    return gemMaterial;
}

function setupPhysicalMaterials()
{
    const diamond = new THREE.MeshPhysicalMaterial();
    const emerald = new THREE.MeshPhysicalMaterial();
    const diamondLarge = new THREE.MeshPhysicalMaterial();

    const defines = {};
    // const isCubeMap = isCubeTexture(envMap)
    // const envMap = environments.main;
    // const envMap = environments.mainEXRCube.texture;
    const envMap = environments.mainEXR;
    const isCubeMap = envMap.isCubeTexture;
    const w = (isCubeMap ? envMap.image[0]?.width : envMap.image.width) ?? 1024
    const cubeSize = w / 4
    const _lodMax = Math.floor(Math.log2(cubeSize))
    const _cubeSize = Math.pow(2, _lodMax)
    const width = 3 * Math.max(_cubeSize, 16 * 7)
    const height = 4 * _cubeSize
    if (isCubeMap)
    {
        defines.ENVMAP_TYPE_CUBEM = ''
        defines.ENVMAP_TYPE_CUBE = ''
    }

    defines.CUBEUV_TEXEL_WIDTH = `${1.0 / width}`
    defines.CUBEUV_TEXEL_HEIGHT = `${1.0 / height}`
    defines.CUBEUV_MAX_MIP = `${_lodMax}.0`
    // Add defines from chromatic aberration
    if (refractionMaterialProps.diamond.aberrationStrength > 0)
    {
        defines.CHROMATIC_ABERRATIONS = ''
    }

    if (refractionMaterialProps.diamond.fastChroma)
    {
        defines.FAST_CHROMA = ''
    }

    const refractionMaterial = Object.assign(new MeshRefractionMaterial(), refractionMaterialProps.diamond);
    renderer.getSize(refractionMaterial.resolution);
    refractionMaterial.side = THREE.FrontSide;
    refractionMaterial.defines = defines;
    refractionMaterial.envMap = envMap;

    applyPhysicalMaterialProps(emerald, physicalMaterialProps.emerald);
    applyPhysicalMaterialProps(diamond, physicalMaterialProps.diamond);
    applyPhysicalMaterialProps(diamondLarge, physicalMaterialProps.diamondLarge);

    physicalMaterials["emerald"] = { front: emerald, back: emerald.clone() }
    physicalMaterials["diamond"] = { front: diamond, back: diamond.clone() }
    physicalMaterials["diamondLarge"] = { front: diamondLarge, back: diamondLarge.clone() }

    physicalMaterials.emerald.back.side = THREE.BackSide

    physicalMaterials.diamond.front.side = THREE.FrontSide
    physicalMaterials.diamond.back.side = THREE.BackSide

    physicalMaterials.diamondLarge.front.side = THREE.FrontSide;
    physicalMaterials.diamondLarge.back.side = THREE.BackSide;
    physicalMaterials.refractionMaterial = refractionMaterial;
    physicalMaterials.default = new THREE.MeshPhysicalMaterial();

    const gemMaterial = Object.assign(new MeshGemMaterial(), refractionMaterialProps.diamond);
    const r = new THREE.Vector2();
    renderer.getSize(r);
    gemMaterial.resolution = r;
    gemMaterial.side = THREE.FrontSide;
    gemMaterial.defines = Object.assign(gemMaterial.defines, defines);
    // gemMaterial.envMap = envMap;
    gemMaterial.envMapD = envMap;
    gemMaterial.reflectivity = 1;
    gemMaterial.roughness = 0.007;
    gemMaterial.metalness = 0;
    gemMaterial.color = new THREE.Color("#ffffff");
    gemMaterial.transmission = 0.032;
    gemMaterial.ior = 2.44;
    gemMaterial.thickness = 1.3;

    console.log(gemMaterial.defines)

    physicalMaterials.gemMaterial = gemMaterial;

    utils.addUniformsToGui(gemMaterial.uniforms, gui)
    // utils.addPhysicalMaterialToGUI(physicalMaterials.default, gui);
    utils.addPhysicalMaterialToGUI(physicalMaterials.gemMaterial, gui);
}

