function getBvh(geometry)
{
    const bvh = new MeshBVHUniformStruct();
    bvh.updateFrom(
        new MeshBVH(geometry.clone().toNonIndexed(), { lazyGeneration: false, strategy: SAH })
    );

    return bvh;
}

