import * as THREE from "three";


const physicalMaterialPropsExceptions = {
    "version": true,
    "defines": true,
    "userData": true,
    "isMaterial": true,
    "isMeshStandardMaterial": true,
    "isMeshPhysicalMaterial": true,
    "type": true,
    "name": true,
    "uuid": true,
};

const physicalMaterialCustomSetups = {
    "bounces": {
        min: 1,
        max: 16,
        step: 1
    },
    "roughness": {
        min: 0,
        max: 1,
        step: .001
    },
    "thickness": {
        min: 0,
        max: 1000,
        step: .1
    },
    "specularIntensity": {
        min: 0,
        max: 1,
        step: .01
    },
    "ior": {
        min: 1,
        max: 3.33,
        step: .001
    },
    "iridescenceIOR": {
        min: 1,
        max: 2.33,
        step: .001
    },
    "metalness": {
        min: 0,
        max: 1,
        step: .001
    },
    "clearcoatRoughness": {
        min: 0,
        max: 1,
        step: .001
    },
    "side": {
        front: THREE.FrontSide,
        back: THREE.BackSide,
        double: THREE.DoubleSide
    }
};

export function addUniformsToGui(uniforms, gui)
{
    const f = gui.addFolder("uniforms");

    const keys = Object.keys(uniforms);
    let i, j;

    for (j = 0; j < keys.length; j++)
    {
        i = keys[ j ];
        // if (uniforms[i].value instanceof THREE.Color)
        // {
        //     f.addColor(uniforms[i].value, "value");
        // }
        if (uniforms[i].value instanceof THREE.Vector2 || uniforms[i].value instanceof THREE.Vector3 || uniforms[i].value instanceof THREE.Vector4)
        {
            const vectorFolder = f.addFolder(i);
            for (let j in uniforms[i].value)
            {
                if (j.indexOf("isVector") !== -1)
                {
                    continue;
                }
                vectorFolder.add(uniforms[i].value, j, -10, 10, .001);
            }
        }
        else if (uniforms[i].value instanceof Array)
        {
            const arrayFolder = f.addFolder(i);
            for (let k = 0; k < uniforms[i].value.length; k++)
            {
                arrayFolder.add(uniforms[i].value, `${k}`, uniforms[i].value[ k ]);
            }
        }
        // Skip nulls for now.
        else if (uniforms[i].value === null)
        {
            continue;
        }
        else if (typeof uniforms[i].value === "number")
        {
            if (physicalMaterialCustomSetups[ i ])
            {
                if (physicalMaterialCustomSetups[ i ].min === undefined)
                {
                    f.add(uniforms[i], "value", physicalMaterialCustomSetups[ i ]).name(i);
                }
                else
                {
                    f.add(
                        uniforms[i], "value",
                        physicalMaterialCustomSetups[ i ].min,
                        physicalMaterialCustomSetups[ i ].max,
                        physicalMaterialCustomSetups[ i ].step
                    ).name(i);
                }
            }
            else
            {
                f.add(uniforms[i], "value", -10, 10, .01).name(i);
            }
        }
        else if (typeof uniforms[i].value === "boolean" || typeof uniforms[i].value === "string")
        {
            f.add(uniforms[i], "value").name(i);
        }
        else if (i.indexOf("Map") !== -1)
        {
            // TODO: Handle individual maps.
            f.add(uniforms, i, {
                NONE: null
            })
        }
        else
        {
            console.log("not added field", i, uniforms[i].value);
        }
    }
}

export function addShaderMaterialToGUI(material, gui)
{
    const f = gui.addFolder(material.name);

    const materialKeys = Object.keys(material);
    materialKeys.sort((a, b) => a > b ? 1 : -1)
    let i, j;

    for (j = 0; j < materialKeys.length; j++)
    {
        i = materialKeys[ j ];
        // Skip if in the exceptions map.
        if (physicalMaterialPropsExceptions[ i ])
        {
            continue;
        }

        // Skip private fields.
        if (i[ 0 ] === "_")
        {
            continue;
        }

        if (material[ i ] instanceof THREE.Color)
        {
            f.addColor(material, i);
        }
        else if (material[ i ] instanceof THREE.Vector2 || material[i] instanceof THREE.Vector3 || material[i] instanceof THREE.Vector4)
        {
            const vectorFolder = f.addFolder(i);
            for (let j in material[ i ])
            {
                if (j.indexOf("isVector") !== -1)
                {
                    continue;
                }
                vectorFolder.add(material[ i ], j, -10, 10, .001);
            }
        }
        else if (material[ i ] instanceof Array)
        {
            const arrayFolder = f.addFolder(i);
            for (let j = 0; j < material[ i ].length; j++)
            {
                arrayFolder.add(material[i], `${j}`, material[ i ][ j ]);
            }
        }
        // Skip nulls for now.
        else if (material[ i ] === null)
        {
            continue;
        }
        else if (typeof material[i] === "number")
        {
            if (physicalMaterialCustomSetups[ i ])
            {
                if (physicalMaterialCustomSetups[ i ].min === undefined)
                {
                    f.add(material, i, physicalMaterialCustomSetups[ i ]);
                }
                else
                {
                    f.add(
                        material, i,
                        physicalMaterialCustomSetups[ i ].min,
                        physicalMaterialCustomSetups[ i ].max,
                        physicalMaterialCustomSetups[ i ].step
                    );
                }
            }
            else
            {
                f.add(material, i, -10, 10, .01);
            }
        }
        else if (typeof material[ i ] === "boolean" || typeof material[i] === "string")
        {
            f.add(material, i);
        }
        else if (i.indexOf("Map") !== -1)
        {
            // TODO: Handle individual maps.
            f.add(material, i, {
                NONE: null
            })
        }
        else
        {
            console.log("not added field", i, material[i]);
        }
    }
}

export function addPhysicalMaterialToGUI (material, gui)
