    if (!(material instanceof THREE.MeshPhysicalMaterial))
    {
        return;
    }

    const f = gui.addFolder(material.name);

    // Add noniterable properties.
    f.add(material, "clearcoat", 0, 1, .001);
    f.add(material, "reflectivity", 0, 1, .001);
    f.add(material, "iridescence", 0, 1, .001);
    f.add(material, "transmission", 0, 1, .001);
    f.add(material, "anisotropy", 0, 1, .001);
    f.add(material, "dispersion", 0, 1, .001);

